
.modal-mask[data-v-3b0b80fc] {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .33);

  transition: opacity .2s ease;
}

/* .modal-wrapper {
  vertical-align: middle;
  height: 100%;
