.modal-container[data-v-3b0b80fc] {
  margin: auto;
  /* padding: 20px 30px; */
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: opacity .2s ease;

  overflow-y: auto;
  width: auto;
  min-width: 250px;
  font-size: 14px;
  padding: 13px 20px 6px 20px;

  max-height: 80%;

  display: inline-block;

  position: fixed;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);
}
.modal-header[data-v-3b0b80fc] {
  margin-top: 0;
  text-align: center;
}
.modal-body[data-v-3b0b80fc] {
  /* display: table-row; */
  margin: 20px 0;
  max-height: 80%;
}
.modal-default-button[data-v-3b0b80fc] {
  float: right;
  color: #006ae8;
  padding: 6px 6px;
  font-size: 15px;
  text-decoration: none;
  margin-top: 5px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
.modal-enter[data-v-3b0b80fc] {
  opacity: 0;
}
.modal-leave-active[data-v-3b0b80fc] {
  opacity: 0;
}

