
.transparent-panel {
  background: rgba(0, 0, 0, 0.66);
  border-radius: 5px;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}
button {
  background-color: cadetblue;
  border: none;
  color: white;
  padding: 6px 16px;
  border-radius: 2px;
  /*text-align: center;*/
  /*text-decoration: none;*/
  /*display: inline-block;*/
  font-size: 16px;
}
button.small {
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 13px;
}
button:disabled {
  background: rgba(211, 211, 211, 0.5);
  color: rgba(0, 0, 0, 0.7);
}
.danger {
  background-color: #f00;
}
.unselectable {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.icon-active {
  fill: #ffa500;
}
.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}
@keyframes spin {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
}
html {
  height: 100%;
}
body {
  /* min-height: 100%; */
  height: 100%;
  overflow: hidden;
  background: url(assets/page-background.jpg) repeat 0 0;
}
.layers {
  overflow: hidden;
  pointer-events: none;
  -ms-flex-item-align: stretch;
      -ms-grid-row-align: stretch;
      align-self: stretch;
}
.mobile-layers {
  position: absolute;
  top: 108px;
  left: 54px;
}
#ll_logos {
  position: absolute;
  bottom: 10px;
  left: 0px;
}
.iframe-modal-content {
  height: 100%;
}
@media screen and (min-width: 769px) {
#left-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: start;
        align-items: flex-start;
    position: absolute;
    left: 8px;
    bottom: 65px;
    top: 100px;
}
}



