
.hidden{
    display: none
}
#measureOptions {
    /* background: rgb(0, 0, 0); */
    background: rgba(255,69,0, 0.66);
    font-size: 14px;
    position: absolute;
    top: 100px;
    right: 280px;
    color: white;
    padding: 10px;
    border-radius: 5px;
    margin: 0;
    z-index: 1000;
}
.tooltip {
  position: relative;
  background: rgba(255,69,0 , 0.5);
  border-radius: 4px;
  color: white;
  padding: 4px 8px;
  opacity: 0.7;
  white-space: nowrap;
}
.tooltip-measure {
  opacity: 1;
  font-weight: bold;
}
.tooltip-static {
  background-color: #ffcc33;
  color: black;
  border: 1px solid white;
}
.tooltip-measure:before,
.tooltip-static:before {
  border-top: 6px solid rgba(255,69,0, 0.5);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  content: "";
  position: absolute;
  bottom: -6px;
  margin-left: -7px;
  left: 50%;
}
.tooltip-static:before {
  border-top-color: #ffcc33;
}
@media screen and (max-width: 768px) {
#measureOptions {
    top: 108px;
    right: auto;
    left: 54px;
}
}



