
#dropTarget {
  width: 300px;
  height: 186px;
  line-height: 186px;
  border: 3px dashed grey;
  font-weight: bold;
  text-align: center;
  font-size: 32px;
  color: #999;
}
#dropTarget.dragover {
  border-color: #319FD3;
}
#dropText {
  display: inline-block;
  vertical-align: middle;
  line-height: normal;
}



